/* Create a custom checkbox */
.Task_checkmark__13XRG {
  position: absolute;
  top: 0;
  left: 0;
  height: 23px;
  width: 23px;
  background-color: #eee;
}

/* On mouse-over, add a grey background color */
.Task_container__Tt6uH:hover input ~ .Task_checkmark__13XRG {
  background-color: #ccc;
}

/* When the checkbox is checked, add a blue background */
.Task_container__Tt6uH input:checked ~ .Task_checkmark__13XRG {
  background-color: #2196F3;
}

/* Create the checkmark/indicator (hidden when not checked) */
.Task_checkmark__13XRG:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.Task_container__Tt6uH input:checked ~ .Task_checkmark__13XRG:after {
  display: block;
}

/* Style the checkmark/indicator */
.Task_container__Tt6uH .Task_checkmark__13XRG:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
